
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

import {postAdmin,postSesionLogin} from '@/server'
export default {
data() {
return {
required:true,//是否必须填写表单
username:'',
password:'',
};
},
components: {
XHeader,
XInput,
Group,
XButton,
Loading,
Alert,
ViewBox
},
methods: {
doLogin(){
this.$vux.loading.show({
text:`请稍等`
})
postAdmin(this.username,this.password).then((result) => {
if(result.status===200){
this.$vux.loading.hide();
if(result.data.code===0){
this.$store.commit('setLogin',{
isLogin:true
})
this.$store.commit('setAdminInfo',result.data.userInfo)
this.$router.push({
name:'Manange'
})
}else{
this.$vux.alert.show({
title: '错误信息提示',
content: `${result.data.message}`,






})
console.log(result);
}
}
}).catch((e) => {
this.$vux.loading.show({
text:`登录超时，请检查网络连接`
})
})
}
},
created(){
postSesionLogin().then((result) => {
console.log(result);
if(result.data.code===0){
this.$store.commit('setLogin',{
isLogin:true
});
this.$store.commit('setAdminInfo',{username:result.data.username});
this.$router.push({
name:'Manange'
})
}
}).catch((e) => {
return
})
}
};



