

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'


import VueCookies from 'vue-cookies'
Vue.use(VueCookies)



import VeGauge from 'v-charts/lib/gauge'
import VePie from 'v-charts/lib/pie'
Vue.component(VeGauge.name, VeGauge)
Vue.component(VePie.name, VePie)

import router from '@/router'


import 'vue-awesome/icons/users'
import 'vue-awesome/icons/cogs'
import 'vue-awesome/icons/user'
import 'vue-awesome/icons/mars'
import 'vue-awesome/icons/venus'
import 'vue-awesome/icons/lock'
import Icon from 'vue-awesome/components/Icon.vue'
Vue.component('icon', Icon);

import vueg from 'vueg'
import 'vueg/css/transition-min.css'
Vue.use(vueg, router)

import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'

Vue.use(LoadingPlugin)
Vue.use(AlertPlugin)

FastClick.attach(document.body)
Vue.config.productionTip = false
/* eslint-disable no-new */

import store from '@/store'

new Vue({
router,
store,
render: h => h(App)
}).$mount('#app-box')



