
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'

import { fail } from "assert";
import {postWeatherSevice} from '@/server'
export default {
data() {
return {
weatherSevice: true,
addressData:ChinaAddressV4Data,
nowcity:['北京'],
baseAvatarUrl:`http://xb.sy1x.com:3000/avatar/`
};
},
components: {
Grid,
GridItem,
ViewBox,
XHeader,
Tabbar,
TabbarItem,
Group,
Cell,
XSwitch,
XAddress
},
created() {

},
computed:{
infoId(){
return this.$route.params.id;
},
usersData(){
return this.$store.state.usersInfo;
},
userInfo(){
let data = this.usersData;
let id = this.infoId;

let userinfo = data.find((item) => {
return item.alias==id
})
return userinfo;
},
genderClass(){
if(this.userInfo.sex==1){
return 'male'
}
if(this.userInfo.sex==2){
return 'female'
}
},
genderIco(){
if(this.userInfo.sex==1){
return 'mars'
}
if(this.userInfo.sex==2){
return 'venus'
}
}
}
,
methods:{
handleWeather(e){
this.$vux.loading.show()
postWeatherSevice(e,this.userInfo.alias).then((result) => {
console.log(result);
this.$vux.alert.show({
title:"信息提示",
content:result.data
})
this.$vux.loading.hide()

})
}
}
};



