
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import { setUserId, getAllUsers } from "@/server";
export default {
components: {
XInput,
XHeader,
Group,
Cell
},
data() {
return {
userId: this.$route.params.id,
cacheId: this.$route.params.id,
isShow: false
};
},
computed: {},
methods: {
changeHandle(value) {
if (value === this.$route.params.id) {
this.isShow = false;
return;
}
this.isShow = true;
},
updateID(cacheId, userId) {
this.$vux.loading.show()
setUserId(cacheId, userId).then(result => {

if (result.data.code === 0) {
this.$vux.loading.hide()
console.log("修改成功");
this.$vux.loading.show()
getAllUsers().then(({ data }) => {
this.$vux.loading.hide()
let resultdata = data.data.sort((a, b) => {
return a.name > b.name;
});

this.$store.commit("setUsersInfo", resultdata);
this.$vux.alert.show({
title:'恭喜',
content:'修改成功'
})
this.$router.push({
path:"/manage"
})
});
}
this.$vux.loading.hide();
this.$vux.alert.show({
title:'抱歉',
content:'修改失败，该ID已经存在'
})
}).catch((e) => {
this.$vux.loading.hide();
this.$vux.alert.show({
title:'抱歉',
content:'小宝微信端通信出错,请稍后再试'
})
console.log(e);
});
}
}
};



