
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import {
postChangeAdminPassword,
postAdminLogout,
getUpdate,
getXiaoBaoState,
setWeatherTime,
getWeatherTime
} from "@/server";
export default {
created() {
getXiaoBaoState().then(result => {
this.xbstate = result.data.data;
});
getWeatherTime().then(result => {
console.log(result);
function formatTime(info) {
let arr = info.split(" ");
let nArr = [];
nArr.push(arr[2]);
nArr.push(arr[1]);
return nArr;
}
let oldTime = [];
let newTime = [];
result.data.message.forEach((item) => {
if(item.id == 'time'){
oldTime = formatTime(item.time);
return;
}
if(item.id == 'newtime'){
newTime = formatTime(item.time);
return;
}
})
this.pushTime = oldTime.join(":");
this.newtime = newTime.join(':')
});
},
components: {
Group,
Cell,
XInput,
XButton,
Datetime
},
data() {
return {
oldPassWord: "",
newPassWord: "",
newPassWord2: "",
showInput: false,
xbstate: { state: "状态获取中。。。" },
pushTime: "09:00",
newtime: ""
};
},
computed: {
adminName() {
return this.$store.state.adminInfo.username;
}
},
methods: {
setTimeValue(t) {

this.$vux.loading.show({
text: "更新中"
});
setWeatherTime(t).then(result => {
this.$vux.loading.hide();
this.$vux.alert.show({
title: "恭喜",
content: result.data.message
});
this.newtime = t;
console.log(result);
});
},
adminLogout() {
postAdminLogout().then(result => {

if (result.data.code === 0) {
this.$store.commit("setLogin", { isLogin: false });
this.$vux.alert.show({
title: "恭喜您",
content: "退出成功",
onHide() {
location.reload();
}
});
}
});
},
changePassword(adminName, oldPassWord, newPassWord) {
return postChangeAdminPassword(adminName, oldPassWord, newPassWord);
},
updateDataToMysql() {
this.$vux.loading.show({
text: "更新中"
});
getUpdate().then(result => {
console.log(result);
if (result.data.code === 0) {
this.$vux.loading.hide();
this.$vux.alert.show({
title: "恭喜",
content: "更新数据成功"
});
return;
}
if (result.data.code === 1) {
this.$vux.loading.hide();
this.$vux.alert.show({
title: "很遗憾",
content: "更新数据失败,XiaoBao系统没有启动"
});
return;
}
});
},
checkVal() {
if (this.oldPassWord === this.newPassWord) {
this.$vux.alert.show({
title: "错误信息提示",
content: "输入的旧密码与新密码不要相同"
});
return;
}
if (this.newPassWord !== this.newPassWord2) {
this.$vux.alert.show({
title: "错误信息提示",
content: "新密码两次输入不一致"
});
return;
}
if (this.newPassWord === this.newPassWord2 && this.oldPassWord != "") {
console.log("可以发送修改密码请求");
console.log(this.adminName);
this.changePassword(
this.adminName,
this.oldPassWord,
this.newPassWord
).then(res => {
if (res.data.code === 0) {
this.showInput = false;
this.oldPassWord = "";
this.newPassWord = "";
this.newPassWord2 = "";
this.$vux.alert.show({
title: "恭喜您",
content: res.data.message
});
} else {
this.$vux.alert.show({
title: "很遗憾",
content: res.data.message
});
}
});
}
}
}
};



