
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default  {
components:{
Group,Cell
},
computed:{
userNumber(){
return this.$store.state.usersInfo.length;
},
userPercent(){
const MAX = 400;
let num = this.$store.state.usersInfo.length;
return parseFloat(num/MAX);
},
userSexNum(){
let num = this.$store.state.usersInfo;
let male = [];
let female = [];
num.forEach(item => {
if(item.sex===1){
male.push(item);
return;
}
if(item.sex===2){
female.push(item);
return;
}
});
return {man:male,woman:female}
}
},
created: function () {
this.chartData = {
columns: ['人数', '性别'],
rows: [
{ '性别': '男', '人数': this.userSexNum.man.length },
{ '性别': '女', '人数': this.userSexNum.woman.length },
{ '性别': '其他', '人数': 0 }
]
}
this.chartSettings = {
dimension: '性别',
metrics: '人数'
}
this.chartData2 = {
columns: ['type', 'value'],
rows: [
{ type: '占比', value: this.userPercent }
]
}
this.chartSettings2 = {
dataType: {
'占比': 'percent'
},
seriesMap: {
'占比': {
min: 0,
max: 1
}
}
}

}
}



